"use client";

import { useState, useRef, useEffect } from "react";
import { useRouter } from "next/navigation";
import { fetchRealFoodPlaces, type FoodPlace } from "@/lib/restaurants";

interface Restaurant {
  id: string;
  name: string;
  category: string;
  rating: number;
  price_range: string;
  location: string;
  vibe: string;
  menu?: string[];
  image: string;
  tip?: string;
  latitude?: number;
  longitude?: number;
}

interface Message {
  id: string;
  content?: string;
  isUser: boolean;
  timestamp: Date;
  restaurants?: Restaurant[];
  isTyping?: boolean;
}

export default function AIAssistantPage() {
  const router = useRouter();
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      content: "Hello! I'm your AI assistant for Lokalista. I can help you find the perfect restaurants, cafes, and events based on your preferences and current location. What would you like to discover today?",
      isUser: false,
      timestamp: new Date(),
    },
  ]);
  const [inputMessage, setInputMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const [userLocation, setUserLocation] = useState<[number, number] | null>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Get user's current location on mount
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setUserLocation([position.coords.latitude, position.coords.longitude]);
        },
        (error) => {
          console.warn('Geolocation error:', error);
          // Default to a generic location if permission denied
          setUserLocation([14.5995, 120.9842]); // Manila, Philippines
        }
      );
    } else {
      // Fallback if geolocation not supported
      setUserLocation([14.5995, 120.9842]);
    }
  }, []);

  // Restaurant database for recommendations
  const restaurantDatabase: Restaurant[] = [
    {
      id: "bintana",
      name: "Bintana Coffee House",
      category: "Filipino Restaurant",
      rating: 4.4,
      price_range: "$$",
      location: "181 K Elizabeth Pond St., Camputhaw, Cebu City",
      vibe: "Art-filled and quiet, ideal for relaxing or working",
      menu: ["Cold Coffee", "Sagada Beans", "Tea and Juice", "Pork Dishes", "Beef (Bintana Scent Beef)", "Fish"],
      image: "/Landing.png",
      tip: "Visit during off-peak hours for a more peaceful session.",
      latitude: 10.3157,
      longitude: 123.8854
    },
    {
      id: "starbucks",
      name: "Starbucks Coffee",
      category: "Cafe",
      rating: 4.3,
      price_range: "$$$",
      location: "Ayala Center Cebu",
      vibe: "Modern and bustling, great for meetings",
      menu: ["Premium Coffee", "Frappuccinos", "Pastries", "Light Meals"],
      image: "/Landing2.png",
      tip: "Peak hours are lunch and afternoon, try early morning for a quieter experience.",
      latitude: 10.3147,
      longitude: 123.8844
    },
    {
      id: "jollibee",
      name: "Jollibee",
      category: "Fast Food",
      rating: 4.2,
      price_range: "$",
      location: "Multiple locations in Cebu City",
      vibe: "Family-friendly and cheerful",
      menu: ["Chickenjoy", "Jolly Spaghetti", "Burger Steak", "Burgers"],
      image: "/wordmark.svg",
      tip: "Most locations are open until late, great for quick meals.",
      latitude: 10.3157,
      longitude: 123.8854
    },
    {
      id: "mang-inasal",
      name: "Mang Inasal",
      category: "Local Cuisine",
      rating: 4.4,
      price_range: "$",
      location: "SM City Cebu",
      vibe: "Authentic Filipino street food experience",
      menu: ["Grilled Chicken", "Pork BBQ", "Sinigang", "Unlimited Rice"],
      image: "/favorite.png",
      tip: "Arrive hungry! Rice and soup are unlimited.",
      latitude: 10.3177,
      longitude: 123.8874
    },
    {
      id: "coffee-bean",
      name: "Coffee Bean & Tea Leaf",
      category: "Cafe",
      rating: 4.2,
      price_range: "$$$",
      location: "Robinsons Galleria Cebu",
      vibe: "Cozy and pet-friendly",
      menu: ["Specialty Coffee", "Tea Blends", "Smoothies", "Pastries"],
      image: "/clock.png",
      tip: "Pet-friendly outdoor seating available.",
      latitude: 10.3197,
      longitude: 123.8894
    },
    {
      id: "chowking",
      name: "Chowking",
      category: "Fast Food",
      rating: 4.1,
      price_range: "$",
      location: "Colon Street, Cebu City",
      vibe: "Quick and budget-friendly",
      menu: ["Dimsum", "Noodles", "Fried Rice", "BBQ"],
      image: "/social.png",
      tip: "Popular at lunch hours, order ahead if possible.",
      latitude: 10.3177,
      longitude: 123.8874
    },
  ];

  const generateRecommendations = (userInput: string): Restaurant[] => {
    const input = userInput.toLowerCase();
    
    // Find matching restaurants based on keywords
    let matches: Restaurant[] = [];
    
    if (input.includes("coffee") || input.includes("cafe") || input.includes("cozy")) {
      matches = restaurantDatabase.filter(r => 
        r.category.toLowerCase().includes("cafe") || 
        r.vibe.toLowerCase().includes("cozy") ||
        r.name.toLowerCase().includes("coffee")
      );
    } else if (input.includes("fast food") || input.includes("quick") || input.includes("cheap")) {
      matches = restaurantDatabase.filter(r => 
        r.category.toLowerCase().includes("fast") || 
        r.price_range === "$"
      );
    } else if (input.includes("budget") || input.includes("affordable")) {
      matches = restaurantDatabase.filter(r => r.price_range === "$");
    } else if (input.includes("local") || input.includes("filipino")) {
      matches = restaurantDatabase.filter(r => 
        r.category.toLowerCase().includes("local") || 
        r.category.toLowerCase().includes("filipino")
      );
    } else if (input.includes("cebu") || input.includes("restaurant")) {
      matches = restaurantDatabase.slice(0, 3); // Show top 3
    }
    
    // If no specific matches, return all
    if (matches.length === 0) {
      matches = restaurantDatabase.slice(0, 3);
    }
    
    return matches;
  };

  const handleSendMessage = async () => {
    if (!inputMessage.trim() || isLoading) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      content: inputMessage.trim(),
      isUser: true,
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage("");
    setIsLoading(true);

    // Simulate AI response with recommendations
    setTimeout(() => {
      const restaurants = generateRecommendations(inputMessage.trim());
      const aiResponse: Message = {
        id: (Date.now() + 1).toString(),
        content: restaurants.length > 0 
          ? `Here are ${restaurants.length} ${inputMessage.trim().toLowerCase().includes('cozy') ? 'cozy coffee spot' : 'recommendations'} in Cebu City you should try:`
          : "Let me help you find great places!",
        restaurants: restaurants,
        isUser: false,
        timestamp: new Date(),
      };
      setMessages(prev => [...prev, aiResponse]);
      setIsLoading(false);
    }, 1500);
  };

  const handleViewOnMap = (restaurant: Restaurant) => {
    if (restaurant.latitude && restaurant.longitude) {
      router.push(`/map?lat=${restaurant.latitude}&lng=${restaurant.longitude}&restaurant=${encodeURIComponent(restaurant.name)}`);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const quickActions = [
    "Good coffee spot with cozy vibes in Cebu",
    "Budget-friendly restaurants",
    "Family-friendly places",
    "Local Filipino cuisine",
    "Fast food options",
    "Cafes for working"
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-white to-pink-50">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-4xl mx-auto px-4 py-6">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 bg-gradient-to-br from-purple-500 to-pink-500 rounded-xl flex items-center justify-center">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
            </div>
          <div>
              <h1 className="text-2xl font-bold text-gray-900">AI Assistant</h1>
              <p className="text-gray-600">Your personal food discovery companion</p>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-4 py-6">
        <div className="bg-white rounded-2xl shadow-xl border border-gray-200 overflow-hidden">
          {/* Messages Container */}
          <div className="h-[600px] overflow-y-auto p-6 space-y-4">
            {messages.map((message) => (
              <div key={message.id} className="space-y-4">
                {/* User Message */}
                {message.isUser && message.content && (
                  <div className="flex justify-end">
                    <div className="max-w-xs lg:max-w-md px-4 py-3 rounded-2xl bg-gradient-to-br from-purple-500 to-pink-500 text-white">
                      <p className="text-sm whitespace-pre-wrap">{message.content}</p>
                      <p className="text-xs mt-1 text-purple-100">
                        {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                      </p>
                    </div>
                  </div>
                )}
                
                {/* AI Message with Restaurant Cards */}
                {!message.isUser && (
                  <div className="space-y-4">
                    {message.content && (
                      <div className="flex justify-start">
                        <div className="max-w-xs lg:max-w-md px-4 py-3 rounded-2xl bg-gray-100 text-gray-900">
                          <p className="text-sm whitespace-pre-wrap">{message.content}</p>
                          <p className="text-xs mt-1 text-gray-500">
                            {message.timestamp.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                          </p>
                        </div>
                      </div>
                    )}
                    
                    {/* Restaurant Cards */}
                    {message.restaurants && message.restaurants.length > 0 && (
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {message.restaurants.map((restaurant) => (
                          <div key={restaurant.id} className="bg-white border border-gray-200 rounded-2xl overflow-hidden shadow-md hover:shadow-lg transition-shadow">
                            {/* Image */}
                            <div className="aspect-video overflow-hidden bg-gray-100">
                              <img 
                                src={restaurant.image} 
                                alt={restaurant.name}
                                className="w-full h-full object-cover"
                                onError={(e) => {
                                  const target = e.target as HTMLImageElement;
                                  target.src = "/Landing.png";
                                }}
                              />
                            </div>
                            
                            {/* Content */}
                            <div className="p-4 space-y-3">
                              <div>
                                <div className="flex items-start justify-between mb-2">
                                  <div>
                                    <h3 className="font-semibold text-gray-900">{restaurant.name}</h3>
                                    <p className="text-sm text-gray-500">{restaurant.category}</p>
                                  </div>
                                  <div className="flex items-center gap-1 text-sm">
                                    <span className="text-yellow-500">⭐</span>
                                    <span className="font-medium">{restaurant.rating}</span>
                                  </div>
                                </div>
                                <p className="text-xs text-gray-400">Closed</p>
                              </div>
                              
                              <div className="space-y-2 text-sm text-gray-700">
                                <div>
                                  <span className="font-medium">📍 Location:</span> {restaurant.location}
                                </div>
                                <div>
                                  <span className="font-medium">✨ Vibe:</span> {restaurant.vibe}
                                </div>
                                <div>
                                  <span className="font-medium">💰 Price:</span> {restaurant.price_range}
                                </div>
                                {restaurant.menu && restaurant.menu.length > 0 && (
                                  <div>
                                    <span className="font-medium">🍽️ Menu:</span> {restaurant.menu.slice(0, 3).join(", ")}
                                  </div>
                                )}
                                {restaurant.tip && (
                                  <div className="bg-amber-50 border border-amber-200 rounded-lg p-2">
                                    <span className="font-medium text-amber-800">💡 Tip:</span> {restaurant.tip}
                                  </div>
                                )}
                              </div>
                              
                              <button
                                onClick={() => handleViewOnMap(restaurant)}
                                className="w-full mt-3 px-4 py-2 bg-gradient-to-r from-purple-500 to-pink-500 text-white rounded-lg hover:opacity-90 transition-opacity text-sm font-medium"
                              >
                                View on Map
                              </button>
                            </div>
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                )}
              </div>
            ))}
            
            {isLoading && (
              <div className="flex justify-start">
                <div className="bg-gray-100 text-gray-900 max-w-xs lg:max-w-md px-4 py-3 rounded-2xl">
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-gradient-to-br from-purple-500 to-pink-500 rounded-full flex items-center justify-center">
                      <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                    </div>
                  </div>
                </div>
          </div>
            )}
            <div ref={messagesEndRef} />
            </div>

          {/* Quick Actions */}
          <div className="px-6 py-4 bg-gray-50 border-t border-gray-200">
            <p className="text-sm text-gray-600 mb-3">Quick actions:</p>
            <div className="flex flex-wrap gap-2">
              {quickActions.map((action, index) => (
                <button
                  key={index}
                  onClick={() => setInputMessage(action)}
                  className="px-3 py-1.5 text-xs bg-white border border-gray-300 rounded-full hover:bg-gray-50 transition-colors"
                >
                  {action}
                </button>
              ))}
            </div>
          </div>

          {/* Input Area */}
          <div className="p-6 bg-white border-t border-gray-200">
            <div className="flex gap-3">
              <div className="flex-1 relative">
                <input
                  type="text"
                  value={inputMessage}
                  onChange={(e) => setInputMessage(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder="Ask anything about places, restaurants, or cafes..."
                  className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  disabled={isLoading}
                />
              </div>
            <button
                onClick={handleSendMessage}
                disabled={!inputMessage.trim() || isLoading}
                className="px-6 py-3 bg-gradient-to-r from-purple-500 to-pink-500 text-white rounded-xl hover:from-purple-600 hover:to-pink-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 flex items-center gap-2"
              >
                {isLoading ? (
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                ) : (
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                  </svg>
                )}
                Send
            </button>
          </div>
        </div>
        </div>

        {/* Features Section */}
        <div className="mt-8 grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200">
            <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-cyan-500 rounded-xl flex items-center justify-center mb-4">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
              </svg>
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Location-Based</h3>
            <p className="text-gray-600 text-sm">Find restaurants and cafes near your current location with precise recommendations.</p>
          </div>

          <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200">
            <div className="w-12 h-12 bg-gradient-to-br from-green-500 to-emerald-500 rounded-xl flex items-center justify-center mb-4">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Smart Filtering</h3>
            <p className="text-gray-600 text-sm">Filter by cuisine, price range, features, and preferences to find exactly what you want.</p>
          </div>

          <div className="bg-white p-6 rounded-xl shadow-lg border border-gray-200">
            <div className="w-12 h-12 bg-gradient-to-br from-orange-500 to-red-500 rounded-xl flex items-center justify-center mb-4">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
              </svg>
            </div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">Personalized</h3>
            <p className="text-gray-600 text-sm">Get recommendations tailored to your preferences, budget, and dining preferences.</p>
          </div>
          </div>
      </div>
    </div>
  );
}